/* CUSTOM CSS */
.App { 
  // position: relative;
  // height: 100vh;
  // display: block;
  .header { 
    width: 100%;
    height: 50px;
    background-color: #7554a0;
    color: #fff;
    display: flex;
    flex-direction: row;
    position: relative;
    font-size: 24px;
    .logo { 
      position: absolute;
      top: 50%; 
      left: 50%;
      transform : translate(-50%,-50%);
      
      i { 
        padding: 0 8px;
      }
    }
    .control {
      position: absolute; 
      right: 0;
      
      top: 50%;
      transform: translateY(-50%);
      button { 
        background-color: transparent;
        padding: 0 8px;
        outline: none;
        border: none;
        font-size: 30px; 
        color: white;
      }
      button:hover { 
        cursor: pointer;

      }
    }
  }
  .main { 
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 50px);
    background: rgb(117,84,160);
    background: linear-gradient( rgba(117,84,160,1) 7%, rgba(117,84,160,1) 27%, rgba(106,95,168,1) 49%, rgba(99,103,174,1) 74%, rgba(87,116,184,1) 100%); 
    overflow: hidden;
    .form { 
     
      padding: 32px;
      display: flex;
      justify-content: center;
      form {
       
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        p { 
          color: rgb(243, 138, 138); 
          font-size: 14px;
        }
        input{ 
            font-size: 18px; 
            
            padding: 8px 16px; 
            margin: 8px 0; 
            border-radius: 5px;
            border: 1px solid #000;
            outline: none;
        }
        button { 
          background-color:rgb(206, 168, 228);
          outline: none;
          border: 1px solid #000;
          padding: 8px 16px;
          border-radius: 5px;
          margin-top: 8px;
        }
        button:hover { 
          cursor: pointer;
          background-color: #7554a0;
          color: white;
        }
      }
  }
  }
}